<template>
  <div class="container">
    <div class="form">
      <query-form :inspect="inspect" ref="queryForm">
      </query-form>
    </div>
    <div class="option">
      <el-button  type="primary" size="small" plain @click="inspectTable">检视</el-button>
      <el-button  type="primary" size="small" plain  @click="exportTable">导出报表</el-button>
    </div>
    <div class="data">
      <div class="title">
        <el-alert
          title="统计截止月份：2018年3月 涉及保单范围：2018年1月1日 - 2018年3月21日"
          type="info"
          :closable="false">
        </el-alert>
      </div>
      <div class="table">
        <base-table
          :columns="columns"
          :data="feeData"
          row-key="id">
        </base-table>
      </div>
    </div>
  </div>
</template>

<script>
const QueryForm = () => import('./QueryForm')
const BaseTable = () => import('@/components/BaseTable')
export default {
  name: 'ConfigFee',
  components: {QueryForm, BaseTable},
  data () {
    return {
      inspect: {
        org: '50000',
        channel: 'ct',
        type: 'month',
        timeRange: '2018Q1',
        version: '50000201801010900'
      },
      columns: [
        {prop: 'configFeeModel', width: 120, label: '配费模式', align: 'center'},
        {
          prop: 'premiumIncome',
          width: 240,
          label: '保费收入',
          align: 'center',
          children: [
            {prop: 'realIncome', width: 120, label: '实际值(万元)', align: 'center'},
            {prop: 'incomePercent', width: 120, label: '占比(%)', align: 'center'}
          ]
        },
        {
          prop: 'operationCostRate',
          width: 120,
          label: '经营成本率',
          align: 'center',
          children: [
            {prop: 'expOperation', width: 120, label: '预算值(%)', align: 'center'},
            {prop: 'realOperation', width: 120, label: '实际值(%)', align: 'center'},
            {prop: 'disOperation', width: 120, label: '二者差(%)', align: 'center'}
          ]
        },
        {
          prop: 'marketCostRate',
          width: 120,
          label: '市场费用率',
          align: 'center',
          children: [
            {prop: 'expMarket', width: 120, label: '预算值(%)', align: 'center'},
            {prop: 'realMarket', width: 120, label: '实际值(%)', align: 'center'},
            {prop: 'disMarket', width: 120, label: '二者差(%)', align: 'center'}
          ]
        },
        {
          prop: 'finalPayRate',
          width: 120,
          label: '终极赔付率',
          align: 'center',
          children: [
            {prop: 'expFinal', width: 120, label: '预算值(%)', align: 'center'},
            {prop: 'realFinal', width: 120, label: '实际值(%)', align: 'center'},
            {prop: 'disFinal', width: 120, label: '二者差(%)', align: 'center'}
          ]
        }
      ],
      feeData: [
        {
          id: '1',
          configFeeModel: '个车分类',
          realIncome: '6424.8',
          incomePercent: '20',
          expOperation: '97.6',
          realOperation: '98.2',
          disOperation: '0.6',
          expMarket: '16.8',
          realMarket: '17.5',
          disMarket: '0.7',
          expFinal: '56.89',
          realFinal: '56.86',
          disFinal: '-0.03'
        },
        {
          id: '2',
          configFeeModel: '车队协议',
          realIncome: '16062',
          incomePercent: '50',
          expOperation: '97.9',
          realOperation: '97.7',
          disOperation: '-0.2',
          expMarket: '20.1',
          realMarket: '21.3',
          disMarket: '1.2',
          expFinal: '59',
          realFinal: '58.6',
          disFinal: '-0.4'
        },
        {
          id: '3',
          configFeeModel: '代理协议',
          realIncome: '9637.2',
          incomePercent: '30',
          expOperation: '97.4',
          realOperation: '97.3',
          disOperation: '-0.1',
          expMarket: '17.2',
          realMarket: '18.5',
          disMarket: '1.3',
          expFinal: '57.2',
          realFinal: '57.6',
          disFinal: '0.4'
        },
        {
          id: '4',
          configFeeModel: '合计',
          realIncome: '32124',
          incomePercent: '100',
          expOperation: '97.69',
          realOperation: '97.68',
          disOperation: '-0.01',
          expMarket: '18.57',
          realMarket: '19.7',
          disMarket: '1.13',
          expFinal: '58.04',
          realFinal: '57.95',
          disFinal: '-0.09'
        }
      ]
    }
  },
  methods: {
    inspectTable () {
    },
    exportTable () {
    },
    sum ({ columns, data }) {
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '合计'
        } else {
          sums[index] = 0
          data.forEach(row => {
            sums[index] += Number(row[column.property])
          })
          if (index >= 3) {
            sums[index] /= data.length
          }
          sums[index] = sums[index].toFixed(2)
        }
      })
      return sums
    }
  }
}
</script>

<style scoped>

</style>



